.custom-avatar {
  width: 40px;
  height: 40px;
  display: flex;
  overflow: hidden;
  position: relative;
  font-size: 1.25rem;
  align-items: center;
  flex-shrink: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  line-height: 1;
  border-radius: 50%;
  justify-content: center;
  -moz-user-select: none;
  color: #fafafa;
  background-color: #bdbdbd;
    margin: 5px;
    cursor: pointer;
}

.rbt-aux {
    height: 0;
    visibility: hidden;
}

.entry__inner {
    display: flex;
    left: 0;
    right: 0;
  }

  .entry__text {
    flex: 1;
    margin-right: 0.5em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  

.avatar-menu-items > li:focus {
  color: #1e2125;
  background-color: #cfd1d37a;
}

.avatar-menu-items > li:active {
  color: #1e2125;
  background-color: #cfd1d3;
}
  